* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

html, body, #root {
  height: 100%;
  max-width: 100vw;
  width: 100%;
  overflow-x: hidden;
}

body {
  overscroll-behavior-y: contain;
}

:root {
  --primary-dark: #042a1c;
  --secondary-dark: #042a1c;
  --accent-blue: #2563eb;
  --light-gray: #f8fafc;
  --text-light: #e3e5e6;
  --text-dark: #1e293b;
  --gradient-start: #042a1c;
  --gradient-end: #042a1c;
}

[data-theme="light"] {
  --primary-dark: #f8fafc;
  --secondary-dark: #e2e8f0;
  --accent-blue: #2563eb;
  --light-gray: #0f172a;
  --text-light: #1e293b;
  --text-dark: #f8fafc;
  --gradient-start: #f8fafc;
  --gradient-end: #e2e8f0;
}

body {
  font-family: 'Inter', sans-serif;
  background-color: var(--primary-dark);
  color: var(--text-light);
  overflow-x: hidden;
  transition: background-color 0.3s ease, color 0.3s ease;
  max-width: 100vw;
  width: 100%;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Poppins', sans-serif;
  font-weight: 600;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.section {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.btn {
  padding: 12px 24px;
  border: none;
  border-radius: 8px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  text-decoration: none;
  display: inline-block;
  font-family: 'Inter', sans-serif;
}

.btn-primary {
  background: var(--accent-blue);
  color: white;
}

.btn-primary:hover {
  background: #1d4ed8;
  transform: translateY(-2px);
}

.btn-secondary {
  background: transparent;
  color: var(--text-light);
  border: 2px solid var(--accent-blue);
}

.btn-secondary:hover {
  background: var(--accent-blue);
  color: white;
}


